"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import { toast } from "sonner";
import { Checkbox } from "@/components/ui/checkbox";
import { Button } from "@/components/ui/button";
import apiClient from "@/lib/AxiosUtils";
import { Permission, User } from "@/interface/types";

export default function UserPermissionPage() {
  const { userId } = useParams() as { userId: string };
  const [selectedUser, setSelectedUser] = useState<User>({} as User);
  const [permissionsState, setPermissionsState] = useState<
    (Permission & { isCheckedNow: boolean })[]
  >([]);
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);

  const getSelectedUser = async (id: string) => {
    try {
      const res = await apiClient.get(`/user/id/${id}`);
      const user: User = res.data.result || {};
      setSelectedUser(user);
    } catch (err) {
      toast.error("Failed to load user.");
    }
  };

  const getAllPermissions = async (userPermissionIds: number[]) => {
    try {
      setLoading(true);
      const res = await apiClient.get(`/permissions/view`);
      const allPermissions: Permission[] = res.data.result || [];

      const enriched = allPermissions.map((perm) => ({
        ...perm,
        isCheckedNow: userPermissionIds.includes(perm.id),
      }));

      setPermissionsState(enriched);
    } catch (err) {
      toast.error("Failed to load permissions.");
    } finally {
      setLoading(false);
    }
  };

  const handleCheckboxChange = (id: number, checked: boolean) => {
    setPermissionsState((prev) =>
      prev.map((perm) =>
        perm.id === id ? { ...perm, isCheckedNow: checked } : perm
      )
    );
  };

  const savePermissions = async () => {
    try {
      setSaving(true);
      const permissionIds = permissionsState
        .filter((perm) => perm.isCheckedNow)
        .map((perm) => perm.id);

      await apiClient.post(`/permissions/update-user-permissions`, {
        userId,
        permissionIds,
      });

      toast.success("Permissions updated successfully.");
    } catch (err) {
      toast.error("Failed to update permissions.");
    } finally {
      setSaving(false);
    }
  };

  useEffect(() => {
    if (userId) {
      getSelectedUser(userId);
    }
  }, [userId]);

  useEffect(() => {
    if (selectedUser?.userId) {
      const assignedPermissionIds = selectedUser.userPermissions?.map((p) => p.id) || [];
      getAllPermissions(assignedPermissionIds);
    }
  }, [selectedUser]);

  const groupedPermissions = permissionsState.reduce((acc, perm) => {
    const [category = "General"] = perm.permissionName.split(":").map((s) => s.trim());
    if (!acc[category]) acc[category] = [];
    acc[category].push(perm);
    return acc;
  }, {} as Record<string, (Permission & { isCheckedNow: boolean })[]>);

  return (
    <div className="max-w-4xl mx-auto px-4 py-6">
      <h2 className="text-xl font-semibold mb-4">
        Edit Permissions for {selectedUser?.userEmail}
      </h2>

      {loading ? (
        <p>Loading permissions...</p>
      ) : (
        <div className="space-y-6">
          {Object.entries(groupedPermissions).map(([category, perms]) => (
            <div key={category}>
              <h3 className="text-md font-semibold text-primary mb-2">{category}</h3>
              <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-3">
                {perms.map((perm) => (
                  <div
                    key={perm.id}
                    className="flex items-start space-x-2 border p-3 rounded-md"
                  >
                    <Checkbox
                      checked={perm.isCheckedNow}
                      onCheckedChange={(checked) =>
                        handleCheckboxChange(perm.id, !!checked)
                      }
                    />
                    <div className="text-sm">
                      <p className="font-medium">
                        {perm.permissionName.split(":")[1]?.trim() || perm.permissionName}
                      </p>
                      <p className="text-muted-foreground text-xs">{perm.description}</p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      )}

      <div className="mt-6 flex justify-end">
        <Button onClick={savePermissions} disabled={saving}>
          {saving ? "Saving..." : "Save Permissions"}
        </Button>
      </div>
    </div>
  );
}
